import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { putUserPass } from "../../apis/ldh/apiuser";

const Drop = ({
  selectedModifyPass,
  setSelectedModifyPass,
  setErrMsg,
  setUserUpdateAt,
  userId,
}) => {
  const [emptyPass, setEmptyPass] = useState("");
  const [emptyModiPass, setEmptyModiPass] = useState("");
  const [emptyDuplPass, setEmptyDuplPass] = useState("");
  const navi = useNavigate();

  const reqData = {
    uid: userId,
    upw: emptyPass,
    newPw: emptyModiPass,
  };

  const isPass = async () => {
    if (!emptyPass || !emptyModiPass || !emptyDuplPass) {
      setErrMsg("  ");
    }
    if (emptyModiPass !== emptyDuplPass) {
      setErrMsg("  ");
    }
    if (emptyPass && emptyPass == (emptyDuplPass || emptyModiPass)) {
      setErrMsg("    ");
    }
    if (
      emptyPass &&
      emptyModiPass &&
      emptyModiPass === emptyDuplPass &&
      emptyPass !== (emptyDuplPass || emptyModiPass)
    ) {
      setErrMsg("");
      const result = await putUserPass(reqData);
      console.log(result);
      if (result.data.statusCode === 1) {
        const now = new Date();
        const year = now.getFullYear();
        const month = String(now.getMonth() + 1).padStart(2, "0");
        const day = String(now.getDate()).padStart(2, "0");
        const hours = String(now.getHours()).padStart(2, "0");
        const minutes = String(now.getMinutes()).padStart(2, "0");
        const seconds = String(now.getSeconds()).padStart(2, "0");
        const formattedTime = `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
        alert("");
        setUserUpdateAt(formattedTime);
        setSelectedModifyPass(false);
        setEmptyDuplPass();
        setEmptyModiPass();
        setEmptyPass();
        localStorage.setItem("user", "");
        navi("/login");
      }
      if (result.data.statusCode === -1) {
        setErrMsg("   ");
      }
      if (result.data.statusCode === -2) {
        setErrMsg("  ,  8~20 ");
      }
    }
  };

  return selectedModifyPass ? (
    <form>
      <div className="form-group">
        <label htmlFor="user"></label>
        <input
          type="password"
          className="form-control"
          id="user"
          value={emptyPass}
          onChange={e => {
            setEmptyPass(e.target.value);
          }}
        />
      </div>
      <div className="form-group">
        <label htmlFor="pwd"> </label>
        <input
          type="password"
          className="form-control"
          id="pwd"
          value={emptyModiPass}
          onChange={e => {
            setEmptyModiPass(e.target.value);
          }}
        />
      </div>
      <div className="form-group">
        <label htmlFor="pwdconfirm">  </label>
        <input
          type="password"
          className="form-control"
          id="confirm"
          value={emptyDuplPass}
          onChange={e => setEmptyDuplPass(e.target.value)}
        />
      </div>
      <button
        type="button"
        className="btn btn-primary"
        onClick={() => {
          isPass();
        }}
      >
        
      </button>
    </form>
  ) : null;
};

export default Drop;
