import React, { useState, useEffect } from "react";
import axios from "axios";
import { SERVER } from "../../apis/config";
import styled from "styled-components";
import Modal from "react-modal";

// Modal  ,    .
Modal.setAppElement("#root");

const UpdatePages = ({ tourData, onUpdate, onCancel }) => {
  const [formData, setFormData] = useState({
    tourScheduleDay: "",
    tourScheduleStart: "",
    tourScheduleEnd: "",
    title: "",
    contents: "",
    cost: "",
    tourColor: "#007bff",
  });
  const [modalIsOpen, setModalIsOpen] = useState(false); // Modal 
  const [errorMessage, setErrorMessage] = useState(""); //   

  useEffect(() => {
    if (tourData) {
      setFormData({
        tourScheduleDay: tourData.tourScheduleDay || "",
        tourScheduleStart: tourData.tourScheduleStart || "",
        tourScheduleEnd: tourData.tourScheduleEnd || "",
        title: tourData.title || "",
        contents: tourData.contents || "",
        cost: tourData.cost || "",
        tourColor: tourData.tourColor || "#007bff",
      });
    }
  }, [tourData]);

  const handleChange = e => {
    const { name, value } = e.target;
    setFormData(prevFormData => ({
      ...prevFormData,
      [name]: value,
    }));
  };

  const handleSubmit = async e => {
    e.preventDefault();
    //  
    const { tourScheduleDay, tourScheduleStart, tourScheduleEnd, title, cost } =
      formData;
    if (
      !tourScheduleDay ||
      !tourScheduleStart ||
      !tourScheduleEnd ||
      !title ||
      !cost
    ) {
      setErrorMessage("   .");
      return;
    }

    if (cost < 0) {
      setErrorMessage("    .");
      return;
    }

    try {
      const response = await axios.put(
        `${SERVER}/api/tour/schedule?tour_schedule_id=${tourData?.tourScheduleId || ""}`,
        {
          tourScheduleId: tourData?.tourScheduleId || "",
          tourScheduleDay,
          tourScheduleStart,
          tourScheduleEnd,
          tourScheduleTitle: title,
          contents: formData.contents,
          cost: parseInt(cost, 10),
        },
      );
      console.log("Successfully updated:", response.data);
      onUpdate();
      closeModal(); //    Modal 
    } catch (error) {
      console.error("Error updating schedule:", error);
      setErrorMessage(" .   .");
    }
  };

  const handleCancel = () => {
    setFormData({
      tourScheduleDay: tourData.tourScheduleDay || "",
      tourScheduleStart: tourData.tourScheduleStart || "",
      tourScheduleEnd: tourData.tourScheduleEnd || "",
      title: tourData.title || "",
      contents: tourData.contents || "",
      cost: tourData.cost || "",
      tourColor: tourData.tourColor || "#007bff",
    });
    onCancel();
    closeModal(); //   Modal 
  };

  const openModal = () => {
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
  };

  return (
    <FormContainer onSubmit={handleSubmit}>
      {errorMessage && <Error>{errorMessage}</Error>}
      <Input
        type="text"
        name="title"
        value={formData.title}
        onChange={handleChange}
        placeholder=""
      />
      <Input
        type="text"
        name="tourScheduleDay"
        value={formData.tourScheduleDay}
        onChange={handleChange}
        placeholder=" "
      />
      <Input
        type="text"
        name="tourScheduleStart"
        value={formData.tourScheduleStart}
        onChange={handleChange}
        placeholder=""
      />
      <Input
        type="text"
        name="tourScheduleEnd"
        value={formData.tourScheduleEnd}
        onChange={handleChange}
        placeholder=""
      />
      <Input
        type="number"
        name="cost"
        value={formData.cost}
        onChange={handleChange}
        placeholder=""
      />
      <Input
        type="text"
        name="contents"
        value={formData.contents}
        onChange={handleChange}
        placeholder=""
      />
      <Input
        type="text"
        name="tourColor"
        value={formData.tourColor}
        onChange={handleChange}
        placeholder=" "
      />
      <Button type="button" onClick={openModal}>
         
      </Button>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        contentLabel=" "
        style={{
          overlay: {
            backgroundColor: "rgba(0, 0, 0, 0.5)",
          },
          content: {
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            margin: "10px",
            width: "300px",
            height: "150px",
            border: "20px",
            borderRadius: "8px",
            boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)",
            backgroundColor: "#fff",
            alignItems: "center",
          },
        }}
      >
        <h2> ?</h2>
        <Button onClick={handleSubmit}></Button>
        <CancelButton onClick={closeModal}></CancelButton>
      </Modal>
      <CancelButton type="button" onClick={handleCancel}>
        
      </CancelButton>
    </FormContainer>
  );
};

export default UpdatePages;

const FormContainer = styled.form`
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
  background-color: #f0f0f0;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
`;

const Input = styled.input`
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
`;

const Button = styled.button`
  padding: 8px 12px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;

  &:hover {
    background-color: #0056b3;
  }
`;

const CancelButton = styled.button`
  padding: 8px 12px;
  background-color: #6c757d;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;

  &:hover {
    background-color: #5a6268;
  }
`;

const Error = styled.div`
  color: red;
  font-size: 14px;
  margin-bottom: 10px;
`;
