import React, { useState, useEffect } from "react";
import Modal from "react-modal";
import styled from "styled-components";
import axios from "axios";
import { SERVER } from "../../apis/config";

// react-modal  ,    .
Modal.setAppElement("#root");

const DeleteModify = ({ tourId, tourScheduleId, onDelete }) => {
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [errorMessage, setErrorMessage] = useState(""); //   

  // useEffect  props  .
  useEffect(() => {
    console.log("tourId:", tourId);
    console.log("tourScheduleId:", tourScheduleId);
  }, [tourId, tourScheduleId]);

  const openModal = () => {
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
    setErrorMessage(""); //      
  };

  const handleDelete = async () => {
    try {
      console.log(
        `${SERVER}/api/tour/schedule?tour_id=${tourId}&tour_schedule_id=${tourScheduleId}`,
      );
      const response = await axios.delete(
        `${SERVER}/api/tour/schedule?tour_id=${tourId}&tour_schedule_id=${tourScheduleId}`,
      );
      console.log(" .", response.data);
      onDelete();
      closeModal();
    } catch (error) {
      console.error(" .", error);
      setErrorMessage(" .   .");
    }
  };

  return (
    <div>
      <DeleteButton onClick={openModal}></DeleteButton>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        contentLabel=" "
        style={{
          overlay: {
            backgroundColor: "rgba(0, 0, 0, 0.5)",
          },
          content: {
            top: "50%",
            left: "50%",
            right: "auto",
            bottom: "auto",
            marginRight: "-50%",
            transform: "translate(-50%, -50%)",
            width: "300px",
            borderRadius: "8px",
            padding: "20px",
            boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)",
          },
        }}
      >
        <ModalContent>
          <h2> ?</h2>
          {errorMessage && <Error>{errorMessage}</Error>}
          <ButtonGroup>
            <ConfirmButton onClick={handleDelete}></ConfirmButton>
            <CancelButton onClick={closeModal}></CancelButton>
          </ButtonGroup>
        </ModalContent>
      </Modal>
    </div>
  );
};

export default DeleteModify;

const DeleteButton = styled.button`
  padding: 10px;
  background-color: #ff4d4f;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;

  &:hover {
    background-color: #d43f3a;
  }
`;

const ModalContent = styled.div`
  text-align: center;
`;

const ButtonGroup = styled.div`
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
`;

const ConfirmButton = styled.button`
  padding: 10px;
  background-color: #ff4d4f;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;

  &:hover {
    background-color: #d43f3a;
  }
`;

const CancelButton = styled.button`
  padding: 10px;
  background-color: #6c757d;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;

  &:hover {
    background-color: #5a6268;
  }
`;

const Error = styled.div`
  color: red;
  margin-top: 10px;
`;
